import { html, PolymerElement } from '@polymer/polymer/polymer-element.js';
import { HAXWiring } from "@lrnwebcomponents/hax-body-behaviors/lib/HAXWiring.js"
/**
 * `scroll-button`
 * `button to scroll to an area or back to top`
 *
 * @microcopy - language worth noting:
 *  -
 *
 * @customElement
 * @polymer
 * @demo demo/index.html
 */
class ScrollButton extends PolymerElement {
  
  // render function
  static get template() {
    return html`
<style>:host {
  display: block;
}

:host([hidden]) {
  display: none;
}
</style>
<slot></slot>
<div>[[target]]</div>
<div>[[icon]]</div>
<div>[[label]]</div>`;
  }

  // haxProperty definition
  static get haxProperties() {
    return {
  "canScale": true,
  "canPosition": true,
  "canEditSource": false,
  "gizmo": {
    "title": "Scroll button",
    "description": "button to scroll to an area or back to top",
    "icon": "icons:android",
    "color": "green",
    "groups": [
      "Button"
    ],
    "handles": [
      {
        "type": "todo:read-the-docs-for-usage"
      }
    ],
    "meta": {
      "author": "btopro",
      "owner": "The Pennsylvania State University"
    }
  },
  "settings": {
    "quick": [],
    "configure": [
      {
        "property": "target",
        "description": "",
        "inputMethod": "array",
        "required": false,
        "icon": "icons:android"
      },
      {
        "property": "icon",
        "description": "",
        "inputMethod": "textfield",
        "required": false,
        "icon": "icons:android"
      },
      {
        "property": "label",
        "description": "",
        "inputMethod": "textfield",
        "required": false,
        "icon": "icons:android"
      }
    ],
    "advanced": []
  }
};
  }
  // properties available to the custom element for data binding
  static get properties() {
    return {
  "target": {
    "name": "target",
    "type": "Object",
    "value": "",
    "reflectToAttribute": false,
    "observer": false
  },
  "icon": {
    "name": "icon",
    "type": "String",
    "value": "icons:expand-less",
    "reflectToAttribute": false,
    "observer": false
  },
  "label": {
    "name": "label",
    "type": "String",
    "value": "Scroll to top",
    "reflectToAttribute": false,
    "observer": false
  }
};
  }

  /**
   * Store the tag name to make it easier to obtain directly.
   * @notice function name must be here for tooling to operate correctly
   */
  static get tag() {
    return "scroll-button";
  }
  /**
   * life cycle, element is afixed to the DOM
   */
  connectedCallback() {
    super.connectedCallback();
    this.HAXWiring = new HAXWiring();
    this.HAXWiring.setup(ScrollButton.haxProperties, ScrollButton.tag, this);
  }
  /**
   * life cycle, element is removed from the DOM
   */
  //disconnectedCallback() {}
  
}
window.customElements.define(ScrollButton.tag, ScrollButton);
export { ScrollButton };
